<template>
  <section id="Contact" class="py-12">
    <v-snackbar v-model="snack" top color="success">
      <span>تم الارسال</span>
      <template v-slot:action="{ on, attrs }">
        <v-btn v-bind="attrs" @click="snack = false" v-on="on" text small
          ><v-icon>mdi-close</v-icon></v-btn
        >
      </template>
    </v-snackbar>
    <v-container class="pt-12">
      <div class="main_heading mb-9">
        <h1>Get In Touch</h1>
        <span class="special_design">
          <i></i>
          <i></i>
          <i></i>
        </span>
      </div>
      <!-- <p>{{ $t("message.hello") }}</p> -->
      <!-- START -->
      <!-- <v-sheet max-width="800" class="d-block mx-auto"> -->
      <v-card max-width="800" class="mx-auto pa-12" flat>
        <v-form
          @submit.prevent="sendMessage"
          method="get"
          id="nativeForm"
          ref="form"
        >
          <v-row>
            <v-col cols="12" sm="6" class="py-0">
              <!-- <label for="fname"></label> -->
              <v-text-field
                outlined
                type="text"
                id="name"
                name="name"
                v-model="name"
                :rules="[rules.required, rules.requiredLength]"
                label="الاسم"
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" class="py-0">
              <v-text-field
                outlined
                type="text"
                id="email"
                name="email"
                v-model="email"
                :rules="[rules.emailRules, rules.required]"
                label="الايميل"
              ></v-text-field>
            </v-col>
            <v-col cols="12" class="">
              <v-text-field
                outlined
                type="text"
                id="subject"
                name="subject"
                v-model="subject"
                :rules="[rules.required, rules.requiredLength]"
                label="الموضوع"
              ></v-text-field>
            </v-col>
            <v-col cols="12" class="py-0">
              <v-textarea
                filled
                outlined
                type="text"
                id="message"
                name="message"
                v-model="message"
                :rules="[rules.required, rules.messageLength]"
                label="الرسالة"
              ></v-textarea>
            </v-col>
          </v-row>
          <v-col cols="12" class="py-0">
            <v-card max-width="300" class="mx-auto">
              <v-btn
                type="submit"
                :loading="loading"
                block
                color="primary"
                x-large
              >
                <v-icon left>mdi-send</v-icon>
                <span>Send Message</span>
                <!-- <span></span> -->
              </v-btn>
            </v-card>
          </v-col>
        </v-form>
      </v-card>
      <!-- </v-sheet> -->
    </v-container>
  </section>
</template>

<script>
export default {
  data: () => ({
    snack: false,
    name: "",
    subject: "",
    email: "",
    message: "",
    dialog: false,
    loading: false,
    rules: {
      messageLength: (v) =>
        (v.length > 3 && v.length < 200) ||
        "length must be more than 3 and less than 200",
      required: (v) => !!v || "required",
      requiredLength: (v) =>
        (v.length > 3 && v.length < 30) ||
        "length must be more than 3 and less than 30",
      emailRules: (v) =>
        /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) ||
        "E-mail must be valid",
    },
  }),
  mounted() {},
  updated() {
    console.log(this.$route);
    console.log(this.$router);
  },
  methods: {
    sendMessage() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        // this.dialog = true;
        setTimeout(() => {
          // this.dialog = false;
          this.loading = false;
          this.snack = true;
        }, 1500);
        setTimeout(() => {
          (function () {
            document.getElementById("nativeForm").submit();
          })();
        }, 2200);
      }
    },
  },
};
</script>

